<template>
    <div>
        <h3>회원가입</h3>
        아이디 : <input type="text" v-model="state.uemail" />
        비밀번호 : <input type="text" v-model="state.upw" />
        이름 : <input type="text" v-model="state.uname" />
        연락처 : <input type="text" v-model="state.uphone" />
        <button @click="handleJoin">회원가입</button>

    </div>
</template>

<script>
import { reactive } from 'vue';
import axios from 'axios';
export default {
    setup () {

        const state = reactive ({
           uemail : '',
           upw : '',
           uname : '',
           uphone : ''
        });

        const handleJoin = async() => {
            const url = `/ROOT/api/customer/join`;
            const headers = {"Content-Type" : "application/json"};
            const body = {
                uemail : state.uemail,
                upw : state.upw,
                uname : state.uname,
                uphone : state.uphone
            };
            const response = await axios.post(url, body, {headers});
            console.log(response.data);

        }

        return {handleJoin, state}
    }
}
</script>

